*, :before, :after {
  box-sizing: inherit;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: #fff;
  margin: 0;
}

.form-container {
  background-color: #e3eaf5;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
  box-shadow: 0 2px 20px rgba(149, 157, 165, .2);
}

.search-form {
  margin-top: 32px;
  margin-bottom: 32px;
  display: flex;
}

.input-field {
  max-width: 300px;
  background: #fff;
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 10px;
  font-size: 17px;
}

.input-field:focus {
  outline: none;
  transition: all .2s ease-in-out;
  box-shadow: 0 1px 9px rgba(0, 0, 0, .2);
}

.search-button {
  color: #212121;
  cursor: pointer;
  max-width: 100px;
  background: #abc0e1;
  border: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px;
  font-size: 17px;
}

.search-button:focus {
  outline: none;
  transition: all .2s ease-in-out;
  box-shadow: 0 4px 4px rgba(149, 157, 165, .2);
}

.search-button:hover {
  background: #7396cd;
  transition: all .2s ease-in-out;
  box-shadow: 0 1px 9px rgba(0, 0, 0, .2);
}

.search-button:after {
  content: "";
  clear: both;
  display: table;
}

.gallery-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  list-style: none;
  display: flex;
}

.info {
  display: flex;
}

.list-item {
  background-color: #e3eaf5;
  border-radius: 30px;
  padding-top: 50px;
  padding-bottom: 50px;
  box-shadow: 0 4px 12px rgba(149, 157, 165, .2);
}

.photo-container {
  width: 500px;
  height: 400px;
  display: flex;
}

.photo-img {
  object-fit: scale-down;
}

.load-more-container {
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  margin-bottom: 24px;
  display: flex;
}

.load-more {
  width: 300px;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .15rem;
  background-color: #e3eaf5;
  border: none;
  border-radius: 20px;
  padding: 16px;
  font-size: .75rem;
  transition: all .2s ease-in-out;
}

.load-more:not([disabled]):hover {
  box-shadow: 0 1px 9px rgba(0, 0, 0, .2);
}

.load-more[disabled] {
  background-color: #eaeaea !important;
}

